@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
* {
    margin: 0;
    padding: 0;
}

* a {
    text-decoration: none;
}

body {
    margin: 0;
    font-family: "Poppins", sans-serif, "Roboto";
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

:root {
    --bkg: #f5f5f7;
    --color: #646868;
    --active: #fff;
    --border: #dbddde;
    --colorCat: #888b8c;
}
.home {
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.home-top {
  width: 100%;
  min-height: 50vh;
  position: relative;
  top: 50%;
}

.home-top .gameshow-content {
  width: 100%;
  height: inherit;
  position: relative;
  top: 10rem;
}

.home-top .gameshow-content .slick-slider {
  height: inherit;
}

.home-top .gameshow-content .slick-slider .slick-list {
  position: relative;
}

.home-top .gameshow-content .slick-slider .slick-list .slick-slide.slick-active.slick-center.slick-current div .oneGame .game {
  transform: translateY(-20px);
  box-shadow: 2px 35px 12px #dbddde;
}

.home-top .gameshow-content .slick-slider .slick-list .slick-slide.slick-active.slick-center.slick-current div .oneGame .game .game-name {
  text-align: center;
  display: block;
  padding: 0.5rem 0;
  font-weight: bold;
}

@media (max-width: 430px) {
  .home-top .gameshow-content .slick-slider .slick-list .slick-slide.slick-active.slick-center.slick-current {
    width: 20rem !important;
  }
  .home-top .gameshow-content .slick-slider .slick-list .slick-slide.slick-active.slick-center.slick-current div .oneGame {
    position: relative;
    left: 3rem !important;
  }
}

.home-top .gameshow-content .gamecarousel {
  width: 100%;
  display: flex;
}

.home-top .gameshow-content .gamecarousel .oneGame {
  padding: 2rem;
}

.home-bottom {
  position: absolute;
  top: 80%;
  width: 100%;
  height: 30vh;
}

.home-bottom-content {
  max-width: 1114px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 3rem 0;
  color: #646868;
  align-items: center;
}

.home-bottom .user {
  color: #000;
}

.home-bottom .user-content {
  display: flex;
  align-items: center;
}

.home-bottom .user-image {
  padding-right: 0.5rem;
}

.home-bottom .user-image img {
  width: 40px;
  border-radius: 50%;
}

.home-bottom .user-name {
  font-weight: 500;
}

.home-bottom .menu-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.home-bottom .menu-icon {
  margin: 0 1rem;
}

.home-bottom .menu-icon a {
  text-decoration: none;
  color: #646868;
  font-size: 2rem;
  font-weight: lighter;
  transition: all 0.2s ease;
}

.home-bottom .menu-icon:hover a {
  position: relative;
  border-radius: 50%;
  padding: 0.5rem 0.8rem;
  background-color: #fff;
  box-shadow: 2px 5px 2px 2px #dbddde;
  overflow: hidden;
}

@media (max-width: 768px) {
  .home-bottom .menu-icon {
    margin: 0 0.5rem;
  }
  .home-bottom .menu-icon a {
    text-decoration: none;
    color: #646868;
    font-size: 1.8rem;
    font-weight: lighter;
  }
}

.home-bottom .info-content {
  display: flex;
  color: #000;
  align-items: center;
}

.home-bottom .info-content span {
  padding-right: 1rem;
}

.home-bottom .info-icon {
  font-size: 1.3rem;
}

@media (max-width: 768px) {
  .home-bottom-content {
    position: relative;
    flex-wrap: wrap;
    padding: 0 1rem;
  }
  .home-bottom .user {
    order: 1;
  }
  .home-bottom .info {
    order: 2;
  }
  .home-bottom .menu {
    position: relative;
    order: 3;
    width: 100%;
    margin: 1rem 0;
  }
}
