.game {
  height: 300px;
  width: 230px;
  transition: 0.3s ease-in;
  cursor: pointer;
}

.game-image {
  background-position-x: 50%;
  background-position-y: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.game-name {
  display: none;
  color: #646868;
}

.game:hover {
  transform: translateY(-20px);
  box-shadow: 2px 35px 12px #dbddde;
}

.game:hover .game-name {
  text-align: center;
  display: block;
  padding: 0.5rem 0;
  font-weight: bold;
}
