.library-category {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  padding: 1rem 0.5rem;
  color: #646868;
  font-weight: 500;
  cursor: pointer;
}

.library-category:hover {
  background-color: #fff;
  transform: translateY(-10px);
  box-shadow: -5px 15px 12px #dbddde;
  border-radius: 5px;
}

