  font-weight: bolder;
}

.library-category .catNumber p {
  position: relative;
  top: -0.8rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.library-games {
  transition: all 0.2s ease;
  height: 100%;
}

.library-games-content {
  display: -ms-grid;
  display: grid;
  grid-gap: 30px;
  gap: 30px;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  -ms-grid-rows: auto;
      grid-template-rows: auto;
  height: 100%;
}

@media (max-width: 1024px) {
  .library-games-content {
    min-height: 100vh;
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    -ms-grid-rows: auto;
        grid-template-rows: auto;
  }
}

@media (max-width: 768px) {
  .library-games {
    transform: translateX(0%) !important;
  }
  .library-games-content {
    min-height: 100vh;
    -ms-grid-columns: (1fr)[2];
        grid-template-columns: repeat(2, 1fr);
    -ms-grid-rows: auto;
        grid-template-rows: auto;
  }
}

@media (max-width: 415px) {
  .library-games {
    transform: translateX(0%) !important;
  }
  .library-games-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}

.library-bottom {
  width: 100%;
  position: fixed;
  top: 90.8%;
  background-color: #fff;
  padding: 1rem 0;
  padding-bottom: 1.7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 999999;
}

.library-bottom .buttons {
  padding: 1rem;
  position: relative;
  top: -0.8rem;
}

.library-bottom .buttons button {
  background: none;
  margin: 0 1rem;
  border: none;
  font-size: 1rem;
  font-weight: 500;
  outline: none;
  cursor: pointer;
}

.library-bottom .buttons button span {
  border-radius: 50%;
  padding: 0.2rem 0.4rem;
  background-color: #000;
  color: #fff;
  text-align: center;
  font-size: 0.8rem;
}

@media (max-width: 768px) {
  .library-bottom .buttons .filter {
    display: none;
  }
}

.library-bottom .return {
  position: relative;
  margin: 0 3rem;
  margin-bottom: 1.5rem;
}

.library-bottom .return a {
  text-decoration: none;
  color: #000;
  font-size: 1.5rem;
  font-weight: lighter;
  transition: all 0.2s ease;
  border-radius: 50%;
  padding: 0.4rem 0.6rem;
}

.library-bottom .return:hover a {
  transform: translateY(10px);
  background-color: #000;
  color: #fff;
  box-shadow: 2px 5px 2px #dbddde;
  overflow: hidden;
}

@media (max-width: 1024px) {
  .library-bottom {
    top: 92%;
  }
}
